#!/usr/bin/env node

import * as program from 'commander'
const packageJSON: any = require('../package.json')

import { initProject } from './project-init'
import { serve } from './serve'

program
  .version(packageJSON["version"])
  .command('init [projectName]')
  .option('-r, --ros-version [version]', 'ROS Version')
  .action(async function (projectName: string | undefined) {
    if (!projectName) {
      console.warn('Specify a project name. Run something like "realm init MyRealmApp"')
      return
    }
    // lets try to read the --ros-version, if not lets default to the the one specified in the default dependencies
    const rosVersion = this.rosVersion || packageJSON._defaultRealmDependencies.ROS_VERSION
    await initProject(projectName, rosVersion)
  });


program.command('start [projectName]')
  .action(async function (projectName: string | undefined) {
    if (!projectName) {
      console.warn('Specify a project name. Run something like "realm start MyRealmApp"')
      process.exit(0)
    }
    await serve(projectName)
  })

program
  .command('go [names...]')
  .option('-p, --peppers', 'Add peppers')
  .action(async function(...commands: any[]){
    console.log(commands[0] || [])
    process.exit()
  })

program.parse(process.argv)
// when typing nothing just run help
if (!program.args.length) program.help();
